* {
  font-family: Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: #333333;
  margin: 30px 0;
  border: 1px solid #666;
}

.app-title {
  font-weight: 300;
  font-size: 28px;
}

.card-grid-group {
  margin: 10px 0;
}
.card-grid-group h3 {
  font-size: 21px;
  margin-bottom: 10px;
}

.card-grid {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 0;
}
.card .img-area {
  height: 150px;
}
.card .img-area img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card p {
  padding: 10px;
  text-align: center;
  font-size: 14px;
}